import appRichText from "../../basic-component/app-rich/parse";

export default {
    name: "bd-detail",

    components: {
        'app-rich-text': appRichText,
    },

    props: {
        detail: {
            type: String,
            default() {
                return '';
            }
        },
    },
    created() {
        this.$store.dispatch('gConfig/setImageWidth', 48);
    },
    computed: {
        newDetail() {
            let detail = '';
            if (this.detail) {
                detail = this.detail;
            }
            return detail;
        },
    }
}
